.animals {
  position: relative;
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 7vh;
  justify-content: center;
  align-items: center;
  max-width: 600px;

  background: url(../../assets/images/boards/single-board.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;

  &__animal {
    width: calc(11.11%);

    margin-top: 5px;
    margin-bottom: 5px;

    height: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    font-size: 1rem;
  }

  &__photo {
    margin-top: 10px;
    max-height: 60%;
    max-width: 100%;
  }

  &__name {
    margin-bottom: 7px;
  }
}
